<template>
    <div>
      <mbread :isOpen="true" :breadSecond="breadSecond" :breadname="breadname"></mbread>
      <el-row>
         <el-card>
           <div class="header">
             <h1 class="h1">{{Title}}</h1>
             <span class="span">撰稿人: </span> <span class="span" v-html="writer"></span>
           </div>
           <div class="content">
             {{desc}}
           </div>
           <el-form v-model="step" ref="form" v-if="isForm" label-width="100">
             <el-form-item label="意见:">
               <el-input type="textarea" class="input"  :autosize="{ minRows: 2, maxRows: 5}" ></el-input>
             </el-form-item>
             <el-form-item label="备注:">
               <el-input type="textarea"  class="input"  :autosize="{ minRows: 2, maxRows: 5}"></el-input>
             </el-form-item>
             <span>是否通过:</span>
             <el-radio v-model="radio" label="1">通过</el-radio>
             <el-radio v-model="radio" label="2">不通过</el-radio>
             <el-button class="submit" type="success" @click="submit">提交</el-button>
           </el-form>

          <div class="footer">
            <el-steps :active="active" finish-status="success">
              <el-step >
                <span slot="title">撰稿人:<span v-html="writer"></span></span>
              </el-step>
              <el-step >
                <span slot="title">审稿人:小王爸</span>
              </el-step>

            </el-steps>
          </div>

         </el-card>

      </el-row>
    </div>
</template>

<script>
  import mbread from '@/components/header/mbread'
  import axios from 'axios'
    export default {
        name: "reportCheck",
        data() {
            return {
              breadSecond: '报告',
              breadname: '报告详情',
              Title: '',
              reportId: '',
              writer: '',
              desc: '',
              active: 1,
              step:{},
              radio: '1',
              isForm: ''
            }
        },
      components:{
        mbread
      },
      methods:{
          _initData() {
            axios.post('/reports/getreport',{
              reportId: this.reportId
            }).then(response =>{
                let res = response.data;
                if (res.status === '0'){
                  this.Title = res.result.title;
                  this.writer = res.result.writer;
                  this.desc = res.result.desc;
                  if (res.result.status === '1'){
                    this.isForm = false;
                    this.active = 2;
                  }else {
                    this.isForm = true;
                  }
                }
            })
          },
        submit() {

        }
      },
      mounted(){
          this.reportId = this.$route.params.reportId;
          if (!this.reportId){
            this.reportId = localStorage.getItem('reportId');
          }
          console.log(this.reportId);
          this._initData();
      }
    }
</script>

<style scoped>
  @media screen and (max-width: 480px){
    .h1{
      height: 20px;
      line-height: 20px;
      font-size: 16px;
      margin-bottom: 30px;
    }
    .content{
      margin-top: 10px;
      text-indent: 4ch;
      line-height: 30px;
      letter-spacing: 1px;
      font-size: 14px;
    }
  }
  .header{
    text-align: center;
  }
  .el-card{
    min-height: 800px;
    width: 100%;
  }
  h1{
    height: 30px;
    line-height: 30px;
    font-size: 18px;
    margin-bottom: 10px;
  }
  .span{
    height: 30px;
    font-size: 14px;
  }
  .content{
    margin: 10px 0;
    text-indent: 4ch;
    line-height: 30px;
    letter-spacing: 1px;
  }
  .footer{
    margin: 20px 0;

  }
  .input{
    display: inline-block;
    width: 80%;
  }
  .el-radio{
    margin: 10px 10px;
  }
  .submit{
    margin-left: 50px;
  }

</style>



